<!-- 文章列表 -->
<template>
  <div class="content">
    <el-backtop> </el-backtop>
    <section>
      <el-row :gutter="20">
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <el-button v-on:click="shiju +=1" type="primary" plain>增加</el-button>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <el-button v-on:click="min" type="success" plain>减少</el-button>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <el-button id="我的博客" v-on:click="buble" type="info" plain>冒泡</el-button>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <el-button v-on:click="bangding" type="warning" plain>双向绑定</el-button>
          </div>
        </el-col>
      </el-row>
      <br />
      <section v-bind:class="{ active: isActive }">结果：{{shiju}}</section>
      <br />
      <section>
        <el-input v-model="input" placeholder="请输入内容"></el-input>
      </section>
    </section>

    <section>
      <el-button  icon="el-icon-search" circle> </el-button>
      <el-button   icon="el-icon-video-play" circle> </el-button>
      <el-button   icon="el-icon-video-pause" circle> </el-button>
      <el-button   icon="el-icon-message" circle></el-button>
      <el-button icon="el-icon-star-off" circle></el-button>
      <el-button icon="el-icon-delete" circle></el-button>
    </section>
    <section>
      <div >
        <tr v-for="(hero,index) in heros" :key="index">
          <td>{{hero.name}}</td>
          <td>{{hero.hp}}</td>
        </tr>
      </div>
    </section>

   
    <section>
      <el-button v-on:click="confirm" type="primary" plain>彩蛋</el-button>
      <el-button v-if="seen" plain>你看到我了</el-button>
    </section>
    <section>
      <iframe
        frameborder="no"
        border="0"
        marginwidth="0"
        marginheight="0"
        width="330"
        height="86"
        src="//music.163.com/outchain/player?type=2&id=458017274&auto=0&height=66"
      ></iframe>
    </section>
  </div>
</template>

<script>
 
export default {
  name: "Share",
  data() {
    //选项 / 数据
    return {
      shiju: 12,
      input: "",
      heros: "",
      seen: false,
      isActive: true,
    };
  },
  methods: {
    //事件处理器
    add: function() {
      this.shiju++;
    },
    min: function() {
      this.shiju--;
    },

    buble: function() {
      this.id = event.currentTarget.id;
      alert(this.id);
    },
    bangding: function() {
      alert(this.input);
    },
    //确认

    confirm: function() {
      this.seen = !this.seen;
     
    },

  
  },

  created() {
    //生命周期函数
    // console.log(this.$route);

    var data = [
      { id: 1, name: "盖伦", hp: 318 },
      { id: 2, name: "提莫", hp: 320 },
      { id: 3, name: "安妮", hp: 419 },
      { id: 4, name: "死歌", hp: 325 },
      { id: 5, name: "米波", hp: 422 }
    ];
    this.heros = data;

 
  
  },
  mounted() {
    //页面元素加载完成
   
  },
  computed: {}
};
</script>

<style>
.active {
  width: 96%;
  height: 20px;
  background: green;
}
/* .content {
  background: #000;
} */
#text {
  display: none;
}
</style>


